.wrapper {
  position: relative;
  overflow: hidden;
}
.btn-blue {
  justify-content: space-between;
  gap: 5px;
  background: #039BE6;
  &:hover {
    opacity: 0.7;
  }
  span {
    line-height: 100%;
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
  }
  i {
    flex-shrink: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
}
.h2 {
  font-size: 50px;
  line-height: 100%;
  font-weight: 500;
  text-transform: uppercase;
}
.t18 {
  position: relative;
  font-size: 18px;
  line-height: 100%;
  font-weight: 500;
  letter-spacing: 2.8px;
  text-transform: uppercase;
  &:before {
    content: '<';
    display: inline-block;
  }
  &:after {
    content: '>';
    display: inline-block;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(15px);
  padding: 0 16px !important;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 60px);
    background: #fff;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      width: calc(100% - 40px);
      margin: 50px auto;
    }
    @media screen and (max-width: 576px) {
      width: calc(100% - 20px);
    }
  }
  &__close {
    position: absolute;
    z-index: 2;
    top: 24px;
    right: 24px;
    width: 48px;
    background: transparent;
    padding: 0;
    &:hover {
      opacity: 0.8;
    }
    img {
      width: 100%;
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 1062px;
    padding: 30px;
  }
  &__top {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
  }
  &__title {
    font-size: 30px;
    line-height: 100%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__subtitle {
    max-width: 380px;
    font-size: 17px;
    line-height: 100%;
    font-weight: 500;
  }
  .form {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
    margin-top: 30px;
    &-group--big {
      grid-column: 2/3;
      grid-row: 1/4;
      textarea {
        height: 100%;
      }
    }
    &-info {
      display: flex;
      align-items: center;
    }
  }
}
.form {
  &-group {
    input {
      width: 100%;
      height: 65px;
      background: transparent;
      border: 1px solid rgba(23, 23, 23, 0.5);
      padding: 0 30px;
      font-size: 16px;
      line-height: 100%;
      color: #171717;
    }
    textarea {
      width: 100%;
      background: transparent;
      border: 1px solid rgba(23, 23, 23, 0.5);
      padding: 20px 30px;
      resize: none;
      font-size: 16px;
      line-height: 100%;
      color: #171717;
    }
  }
  &-info {
    max-width: 370px;
    text-align: right;
    padding-right: 30px;
    font-size: 14px;
    line-height: 100%;
    margin-left: auto;
    a:hover {
      opacity: 0.7;
    }
  }
  &-btn {
    height: 75px;
    padding: 10px 32px;
    span {
      font-size: 16px;
    }
    i {
      width: 15px;
    }
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 42px;
  }
  .t18 {
    font-size: 16px;
  }
  .modal-form {
    .modal__dialog {
      margin-top: 98px;
    }
    &__top {
      grid-template-columns: 100%;
    }
    .form {
      grid-template-columns: 100%;
      &-group--big {
        grid-column: unset;
        grid-row: unset;
        textarea {
          height: 150px;
        }
      }
      &-info {
        max-width: 340px;
        text-align: start;
        padding: 0;
        margin: 0;
      }
    }
  }
  .form {
    &-group {
      input {
        height: 50px;
        padding: 0 20px;
      }
      textarea {
        padding: 20px;
      }
    }
    &-btn {
      height: 50px;
      padding: 5px 25px;
    }
  }
}

@media screen and (max-width: 576px) {
  .h2 {
    font-size: 30px;
  }
  .t18 {
    font-size: 13.5px;
    letter-spacing: 2.1px;
  }
  .modal-form {
    .modal__dialog {
      padding: 30px 20px;
    }
  }
  .form {
    &-group {
      input,
      textarea{
        font-size: 14px;
      }
    }
    &-btn {
      span {
        font-size: 14px;
      }
    }
  }
}