.main {
  margin: 20px 20px 0 20px;
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    img {
      width: 100%;
      height: 100%;
      min-height: 825px;
      display: block;
      border-radius: 20px;
    }
  }
  &__desc {
    position: relative;
  }
  &__group {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    padding: 285px 0 150px;
  }
  &__title {
    position: relative;
    text-transform: uppercase;
    display: inline-block;
    font-size: 64px;
    line-height: 140%;
    font-weight: 500;
    color: #fff;
    .circles {
      position: absolute;
      top: 15px;
      right: 0;
      display: flex;
      align-items: stretch;
    }
    .circle,
    .circle2,
    .circle3,
    .circle4,
    .circle5 {
      width: 39px;
      height: 39px;
      background: #fff;
      border-radius: 50%;
    }
    .circle3,
    .circle5{
      background: transparent;
      border: 3px solid #fff;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 100%;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 2.4px;
    color: #fff;
    margin-top: 40px;
  }
  &__btn {
    min-width: 421px;
    height: 96px;
    padding: 8px 32px;
    margin-top: 56px;
    span {
      font-size: 20px;
      letter-spacing: 1.6px;
    }
    i {
      width: 25px;
    }
  }
  &__socials {
    display: none;
    justify-content: start;
    align-items: start;
    gap: 5px;
    margin-top: 30px;
  }
  &__social {
    max-width: 44px;
    img {
      max-width: 100%;
    }
  }
}
.about {
  padding: 140px 0 90px;
  &__subtitle {
    opacity: 0.5;
  }
  &__title {
    margin-top: 40px;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 40px;
    margin-top: 80px;
  }
  &__item {
    max-width: 850px;
    width: 50%;
    &:nth-child(2) {
      max-width: 688px;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      p {
        font-size: 20px;
        line-height: 100%;
        font-weight: 500;
      }
    }
    &-year {
      position: relative;
      display: inline-block;
      font-family: 'drukwidecyr';
      font-size: 66px;
      line-height: 100%;
      font-weight: 500;
      color: #039BE6;
      margin-bottom: 40px;
      i {
        position: absolute;
        top: 5px;
        left: calc(100% + 10px);
        img {
          max-width: 550px;
        }
      }
    }
  }
  &__wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 100px;
    margin-top: 160px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5 - 180px / 5));
    gap: 45px;
    margin-top: 40px;
  }
  &__text {
    max-width: 750px;
    text-align: center;
    margin: 80px auto 0;
    p {
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
    }
  }
}
.about-card {
  display: flex;
  flex-direction: column;
  &__img {
    margin-bottom: auto;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 100%;
    font-weight: 500;
    margin-top: 20px;
  }
}
.info {
  padding: 90px 0 215px;
  &__items {
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5 - 336px / 5));
    gap: 84px;
    margin-top: 112px;
  }
  &__item {
    &:nth-child(even) {
      padding-top: 80px;
    }
    &-number {
      font-family: 'drukwidecyr';
      font-size: 80px;
      line-height: 100%;
      font-weight: 500;
      opacity: 0.18;
    }
    &-title {
      font-size: 26px;
      line-height: 100%;
      font-weight: 700;
      margin-top: 40px;
    }
    &-text {
      margin-top: 40px;
      p {
        font-size: 14px;
        line-height: 100%;
        font-weight: 500;
      }
    }
  }
}
.cases {
  &__group {
    max-width: 1880px;
    width: calc(100% - 40px);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
    margin: 0 auto;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &:nth-child(even) {
      .cases__group-inner {
        margin-left: auto;
      }
    }
  }
  &__group-inner {
    position: relative;
    max-width: 560px;
    min-height: 850px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    padding: 60px 0;
    color: #fff;
  }
  &__img {
    display: none;
    img {
      border-radius: 20px;
    }
  }
  &__title2 {
    position: absolute;
    top: 70px;
    left: 0;
    opacity: 0.24;
  }
  &__subtitle {
    position: absolute;
    top: 50px;
    left: 0;
    opacity: 0.5;
  }
  &__text {
    margin-top: 50px;
    p {
      font-size: 19px;
      line-height: 100%;
      font-weight: 500;
    }
  }
  &__btn {
    height: 63px;
    min-width: 270px;
    display: inline-flex;
    justify-content: space-between;
    gap: 5px;
    background: transparent;
    border: 2px solid #fff;
    padding: 10px 19px 10px 39px;
    margin-top: 50px;
    &:hover {
      background: #fff;
      span {
        color: #171717;
      }
      i svg path {
        fill: #171717;
      }
    }
    span {
      font-size: 14px;
      line-height: 100%;
      font-weight: 500;
      color: #fff;
      transition: all 0.225s;
    }
    i {
      width: 11px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.225s;
      }
    }
  }
}
.office {
  padding: 175px 0 60px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  &__desc {
    width: 45%;
    max-width: 742px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 60px;
    p {
      font-size: 19px;
      line-height: 129%;
      font-weight: 500;
    }
  }
  &__video {
    position: relative;
    width: 55%;
    &-img {
      display: block;
      img {
        width: 100%;
        min-height: 300px;
        border-radius: 20px;
      }
    }
    &-play {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 140px;
      height: 140px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 7px solid #fff;
      border-radius: 50%;
      img {
        max-width: 48px;
      }
    }
  }
}
.contacts {
  position: relative;
  padding: 70px 0;
  &__map {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: calc(100% - 40px);
    height: 100%;
    iframe {
      width: 100%;
      height: 100%;
      border: none;
      border-radius: 20px;
    }
  }
  .map {
    width: 100%;
    height: 100%;
  }
  &__desc {
    position: relative;
    z-index: 2;
    max-width: 270px;
    background: #fff;
    border-radius: 10px;
    padding: 30px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__item {
    &-title {
      font-size: 17px;
      line-height: 138%;
      font-weight: 500;
    }
    &-text {
      font-size: 17px;
      line-height: 138%;
    }
  }
  &__phone {
    position: relative;
    display: inline-block;
    padding-left: 15px;
    font-size: 18px;
    line-height: 100%;
    font-weight: 700;
    letter-spacing: 0.8px;
    margin-top: 35px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 7px;
      height: 7px;
      background: #039BE6;
      border-radius: 50%;
    }
    &:hover {
      color: #039BE6;
    }
  }
  &__subgroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    margin-top: 35px;
  }
  &__info {
    font-size: 13px;
    line-height: 138%;
    font-weight: 500;
  }
  &__socials {
    display: flex;
    align-items: start;
    gap: 10px;
  }
  &__social {
    max-width: 30px;
    &:hover {
      opacity: 0.7;
    }
    img {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .about {
    padding-top: 90px;
    &__wrap {
      margin-top: 80px;
    }
  }
  .info {
    padding: 90px 0 180px;
    &__items {
      grid-template-columns: repeat(5, calc(100% / 5 - 160px / 5));
      gap: 40px;
      margin-top: 80px;
    }
    &__item {
      &-number {
        font-size: 60px;
      }
      &-title {
        font-size: 19px;
        margin-top: 30px;
      }
      &-text {
        margin-top: 30px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    margin: 0;
    &__group {
      padding-top: 255px;
    }
  }
  .office {
    padding-top: 60px;
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
      max-width: 100%;
    }
    &__video {
      width: 100%;
    }
  }
}
@media screen and (max-width: 992px) {
  .main {
    .swiper-slide {
      img {
        min-height: 680px;
      }
    }
    &__group {
      padding: 120px 0 35px;
    }
    &__title {
      font-size: 42px;
      .circles {
        top: 10px;
      }
      .circle,
      .circle2,
      .circle3,
      .circle4,
      .circle5 {
        width: 22px;
        height: 22px;
      }
    }
    &__subtitle {
      font-size: 16px;
      margin-top: 30px;
    }
    &__btn {
      min-width: 340px;
      height: 68px;
      padding: 5px 25px;
      margin-top: 30px;
      span {
        font-size: 16px;
      }
      i {
        width: 20px;
      }
    }
    &__socials {
      display: flex;
    }
  }
  .about {
    padding: 60px 0 30px;
    &__title {
      margin-top: 30px;
    }
    &__items {
      flex-direction: column;
      gap: 40px;
      margin-top: 30px;
    }
    &__item {
      width: 100%;
      &-text {
        p {
          font-size: 16px;
        }
      }
      &-year {
        font-size: 36px;
        i {
          img {
            max-width: 290px;
          }
        }
      }
    }
    &__wrap {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
      margin-top: 60px;
    }
    &__cards {
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__text {
      margin-top: 40px;
      p {
        font-size: 16px;
      }
    }
  }
  .about-card {
    &__text {
      font-size: 16px;
    }
  }
  .info {
    padding: 30px 0 60px;
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
      margin-top: 60px;
    }
    &__item {
      &:nth-child(even) {
        padding-top: 0;
      }
    }
  }
  .cases {
    &__group {
      width: 100%;
      background: unset !important;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
    &__group-inner {
      max-width: 100%;
      min-height: unset;
      padding: 0;
      color: #171717;
    }
    &__title2 {
      position: static;
      opacity: 1;
      margin-bottom: 30px;
    }
    &__img {
      width: calc(100% + 40px);
      display: block;
      margin-left: -20px;
      img {
        width: 100%;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    &__desc {
      margin-top: 30px;
    }
    &__text {
      margin-top: 20px;
      p {
        font-size: 16px;
      }
    }
    &__btn {
      border-color: #171717;
      margin-top: 20px;
      &:hover {
        background: #171717;
        span {
          color: #fff;
        }
        i svg path {
          fill: #fff;
        }
      }
      span {
        color: #171717;
      }
      i {
        svg path {
          fill: #171717;
        }
      }
    }
  }
  .office {
    padding-bottom: 30px;
    &__text {
      margin-top: 30px;
      p {
        font-size: 16px;
      }
    }
    &__video {
      &-play {
        width: 90px;
        height: 90px;
        img {
          max-width: 30px;
        }
      }
    }
  }
  .contacts {
    padding: 0;
    &__map {
      position: static;
      transform: translate(0);
      width: 100%;
      height: 480px;
    }
    &__desc {
      margin: -50px auto 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
      pointer-events: none;
    }
    .swiper-slide {
      img {
        border-radius: 0;
      }
    }
    &__desc {
      z-index: 3;
    }
    &__group {
      width: 100%;
    }
    &__title {
      font-size: 28px;
      .circles {
        top: 5px;
      }
    }
    &__subtitle {
      font-size: 14px;
    }
    &__btn {
      width: 100%;
      min-width: unset;
      span {
        font-size: 14px;
        letter-spacing: 1.14px;
      }
    }
    &__socials {
      justify-content: center;
    }
  }
  .about-card {
    &__text {
      font-size: 14px;
      margin-top: 10px;
    }
  }
  .info {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      align-items: start;
      gap: 20px;
    }
    &__item {
      &:nth-child(even) {
        padding-top: 30px;
      }
      &:last-child {
        width: calc(50% - 10px);
        grid-column: 1/3;
        margin: 0 auto;
      }
      &-text {
        p {
          font-size: 12px;
        }
      }
    }
  }
  .cases {
    &__img {
      width: calc(100% + 20px);
      margin-left: -10px;
      img {
        border-radius: 0;
      }
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__btn {
      width: 100%;
    }
  }
  .office {
    &__text {
      p {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .main {
    &__title {
      font-size: 24px;
      .circles {
        top: 0;
      }
      .circle,
      .circle2,
      .circle3,
      .circle4,
      .circle5 {
        width: 18px;
        height: 18px;
      }
    }
  }
}
