.footer {
  padding: 50px 0 25px;
}
.footer-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  &__logo {
    width: 273px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__right {
    max-width: 1080px;
    width: 100%;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  &__subtitle {
    position: relative;
    font-size: 23px;
    line-height: 100%;
    font-weight: 500;
    letter-spacing: 6.72px;
    text-transform: uppercase;
    white-space: nowrap;
    margin-bottom: 45px;
    opacity: 0.34;
    &:before {
      content: '<';
      display: inline-block;
    }
    &:after {
      content: '>';
      display: inline-block;
    }
  }
  &__text {
    max-width: 360px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    p {
      font-size: 14px;
      line-height: 100%;
      font-weight: 500;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li a {
      font-size: 17px;
      line-height: 138%;
      font-weight: 500;
      &:hover {
        color: #039BE6;
      }
    }
  }
  .contacts__phone {
    margin-top: 0;
  }
}
.footer-bottom {
  margin-top: 100px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 20px;
  }
  &__text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    &:nth-child(2) {
      max-width: 850px;
      width: 100%;
    }
    p {
      font-size: 12px;
      line-height: 100%;
      font-weight: 500;
      a:hover {
        color: #039BE6;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer-bottom {
    margin-top: 80px;
    &__text {
      &:nth-child(2) {
        width: auto;
        max-width: unset;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding-top: 40px;
  }
  .footer-top {
    &__inner {
      flex-direction: column;
    }
    &__subtitle {
      font-size: 20px;
      margin-bottom: 30px;
    }
    &__text {
      p {
        font-size: 12px;
      }
    }
    .contacts__subgroup {
      flex-direction: column;
      align-items: start;
    }
  }
  .footer-bottom {
    margin-top: 40px;
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__text {
      flex-direction: column;
      align-items: start;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__left {
      width: 100%;
    }
    &__logo {
      margin: 0 auto;
    }
    &__items {
      flex-wrap: wrap;
      gap: 30px;
    }
    &__item {
      &:nth-child(1) {
        width: 100%;
      }
    }
    &__subtitle {
      font-size: 16px;
    }
    &__text {
      max-width: 100%;
    }
  }
}