.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: calc(100% - 40px);
  padding: 45px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__logo {
    width: 345px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 50px;
    li a {
      font-size: 18px;
      line-height: 100%;
      font-weight: 700;
      letter-spacing: 0.8px;
      text-transform: uppercase;
      color: #fff;
      &:hover {
        color: #039BE6;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  &__social {
    max-width: 43px;
    &:hover {
      opacity: 0.7;
    }
    img {
      max-width: 100%;
    }
  }
}
.burger {
  position: relative;
  width: 37px;
  height: 22px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    height: 2px;
    background: #fff;
    border-radius: 4px;
  }
  span:nth-child(1) {
    top: 0;
    right: 0;
    width: 24px;
  }
  span:nth-child(2) {
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
  }
  span:nth-child(3) {
    left: 0;
    bottom: 0;
    width: 24px;
  }
}
.header-menu {
  display: none;
}

@media screen and (max-width: 1200px) {
  .header {
    width: 100%;
    padding: 30px 0;
    &__logo {
      width: 240px;
    }
    &__list {
      gap: 24px;
      li a {
        font-size: 16px;
      }
    }
    &__socials {
      gap: 5px;
    }
    &__social {
      max-width: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__inner {
      .header__list,
      .header__socials {
        display: none;
      }
    }
    &__subgroup {
      display: flex;
      align-items: center;
      gap: 12px;
    }
    &__phone {
      position: relative;
      padding-left: 15px;
      font-size: 18px;
      line-height: 100%;
      font-weight: 700;
      letter-spacing: 0.8px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 7px;
        height: 7px;
        background: #039BE6;
        border-radius: 50%;
      }
      &:hover {
        opacity: 0.7;
      }
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 30px;
    }
    &__item {
      &-title {
        font-size: 14px;
        line-height: 100%;
        font-weight: 700;
      }
      &-text {
        font-size: 14px;
        line-height: 100%;
        font-weight: 500;
      }
    }
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 100%;
      height: 68px;
      padding: 5px 25px;
      span {
        font-size: 14px;
        letter-spacing: 1.14px;
      }
      i {
        width: 15px;
      }
    }
  }
  .burger {
    display: block;
  }
  .header-menu {
    position: fixed;
    z-index: 10;
    top: 0;
    right: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    display: block;
    pointer-events: none;
    &--active {
      pointer-events: auto;
      .header-menu__inner {
        transform: translateX(0);
      }
      .header-menu__bg {
        opacity: 1;
      }
      .header-menu__close {
        display: block;
      }
    }
    &__bg {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background: rgba(0, 0, 0, 0.8);
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
    }
    &__close {
      position: fixed;
      top: 15px;
      left: 15px;
      width: 38px;
      display: none;
      &:hover {
        opacity: 0.7;
      }
      img {
        width: 100%;
      }
    }
    &__inner {
      position: relative;
      z-index: 2;
      transform: translateX(400px);
      max-width: 400px;
      height: calc(100 * var(--vh));
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 30px;
      background: #fff;
      padding: 30px 15px 98px 15px;
      margin-left: auto;
      transition: all 0.3s;
      overflow-y: auto;
    }
    .header__list {
      flex-direction: column;
      align-items: start;
      gap: 30px;
      li a {
        color: #171717;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header-menu {
    &__inner {
      max-width: 270px;
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__logo {
      width: 200px;
    }
    &__phone {
      font-size: 16px;
    }
  }
  .header-menu {
    &__inner {
      max-width: 250px;
    }
  }
}